.rotate {
    width: 100%;
    height: 100%;
    position: absolute;
}
.counterrotate {
    width: 21px;
    height: 21px;
    border-radius: 20px;
    background-color: white;
}
.circle1 {
    -webkit-transform: rotateZ(0deg);
    transform: rotateZ(0deg);
}
.circle2 {
    -webkit-transform: rotateZ(45deg);
    transform: rotateZ(45deg);
}
.circle3 {
    -webkit-transform: rotateZ(90deg);
    transform: rotateZ(90deg);
}
.circle4 {
    -webkit-transform: rotateZ(135deg);
    transform: rotateZ(135deg);
}
.circle5 {
    -webkit-transform: rotateZ(180deg);
    transform: rotateZ(180deg);
}
.circle6 {
    -webkit-transform: rotateZ(225deg);
    transform: rotateZ(225deg);
}
.circle7 {
    -webkit-transform: rotateZ(270deg);
    transform: rotateZ(270deg);
}
.circle8 {
    -webkit-transform: rotateZ(315deg);
    transform: rotateZ(315deg);
}
.circle1 .counterrotate {
    -webkit-animation: preload1 .8s linear infinite;
            animation: preload1 .8s linear infinite;
}
.circle2 .counterrotate {
    -webkit-animation: preload2 .8s linear infinite;
            animation: preload2 .8s linear infinite;
