.login-form {
    color: black;
}
.login-form h3 {
    font-size: 16px;
    text-align: center;
    padding-bottom: 20px;
}
.login-input-box {
    position: relative;
    width: 100%;
}
.login-input-box input {
    width: 100%;
    font-size: 15px;
    padding: 20px 0;
    border: none;
    outline: none;
    background-color: transparent;
    position: relative;
    z-index: 2;
}
.login-input-title {
    position: absolute;
    font-weight: bold;
    top: 18px;
    left: 0;
    -webkit-transform-origin: 0%;
            transform-origin: 0%;
    z-index: 1;
    transition: all .3s ease;
    font-size: 16px;
}
.login-input-box i {
    position: absolute;
    right: 0;
    top: 50%;
    -webkit-transform: translateY(-55%);
            transform: translateY(-55%);
    color: #CAA846;
    z-index: 3;
}
.login-input-box::before {
    position: absolute;
    content: '';
    width: 100%;
    bottom: 0;
    left: 0;
    right: 0;
    border-top: 2px solid #333333;
}
.login-input-box::after {
    position: absolute;
    content: '';
    width: 100%;
    bottom: 0;
    left: 0;
    right: 0;
    border-top: 2px solid #777777;
    -webkit-transform: scale(0, 1);
            transform: scale(0, 1);
    -webkit-transform-origin: 0%;
            transform-origin: 0%;
    transition: -webkit-transform .5s ease-in-out;
    transition: transform .5s ease-in-out;
    transition: transform .5s ease-in-out, -webkit-transform .5s ease-in-out;
}
.login-input-focused::after {
    -webkit-transform: scale(1, 1);
            transform: scale(1, 1);
}
.login-input-focused .login-input-title {
    top: 2px;
    font-size: 11px;
    color: black;
}
.login-checkbox {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 30px 0;
}
.login-checkbox input {
    position: absolute;
    width: 0;
    height: 0;
}
.login-checkbox span {
    width: 20px;
    height: 20px;
    background-color: transparent;
    border-radius: 4px;
    border: 2px solid black;
    position: absolute;
    left: 0;
    top: 2px;
}
.login-checkbox input:checked ~ span {
    background-color: black;
}
.login-checkbox span:after {
    content: "";
    position: absolute;
    display: none;
}
.login-checkbox input:checked ~ span:after {
    display: block;
}
.login-checkbox span:after {
    left: 5px;
    top: 0;
    width: 7px;
    height: 12px;
    border: solid white;
    border-width: 0 2px 2px 0;
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
}
.login-checkbox label {
    padding-left: 25px;
    position: relative;
    font-weight: bold;
}
